<template>

  <div class="container">
    
    <h1>CloudPlayer</h1>

    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown = 0"
      @dismiss-count-down="countDownChanged"
    >
      {{ mensaje.texto }}
    </b-alert>

    <form @submit.prevent="editarTitle(playlistEditar)" v-if="editar">
      <h3>Editar Playlist</h3>

      <input
        type="text"
        class="form-control my-2"
        placeholder="Title"
        v-model="playlistEditar.title"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Artista"
        v-model="playlistEditar.artist"
      />

      <input
        type="text"
        class="form-control my-2"
        placeholder="Ruta"
        v-model="playlistEditar.ruta"
      />

      <b-button class="btn-success my-2 mx-2" type="submit">Editar</b-button>
      <b-button class="my-2" type="submit" @click="editar = true"
        >Cancelar</b-button
      >
    </form>
    <form @submit.prevent="agregarPlaylist()" v-if="!editar">
      <h3>Agregar nueva canción</h3>

      <input
        type="text"
        class="form-control my-2"
        placeholder="Titulo"
        v-model="playlist.title"
      />
      <input
        type="text"
        class="form-control my-2"
        placeholder="Artista"
        v-model="playlist.artist"
      />

      <input
        type="text"
        class="form-control my-2"
        placeholder="Ruta"
        v-model="playlist.ruta"
      />

      <b-button class="btn-success my-2" type="submit">Agregar</b-button>
    </form>

      <div class="PlayList">
      <PlayerControlsBars 
        :loop="loop"
        :shuffle="shuffle"
        :progress="progress"
        :playtrack="play"
        @pausetrack="pause"
        @stoptrack="stop"
        @skiptrack="skip"
        @toggleloop="toggleLoop"
        @toggleshuffle="toggleShuffle"
        @updateseek="setSeek"/>
      </div>
      

    <table class="table">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Titulo</th>
          <th scope="col">Artista</th>
          <th scope="col">Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in playlist" :key="index">
          <th scope="row">{{ item._id }}</th>
          <td>{{ item.title }}</td>
          <td>{{ item.artist }}</td>
          <td>
            <b-button
              class="btn-danger mx-2"
              @click="eliminarPlaylist(item._id)"
              >Eliminar</b-button
            >
            <b-button class="btn-warning mx-2" @click="activarEdicion(item._id)"
              >Editar</b-button
            >
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>

import PlayerControlsBars from '@/components/PlayerControlsBars.vue'

export default {

    name: "Playlist",
    components: {PlayerControlsBars},

  data() {
    return {
      playlist: [],
      mensaje: { color: "success", texto: "" },
      dismissSecs: 5,
      dismissCountDown: 0,

      playlist: { title: "", artist: "", ruta: "" },
      editar: false,
      playlistEditar: {},
    };
  },

  created() {
    this.listarPlaylist();
  },

  methods: {
    listarPlaylist() {
      this.axios
        .get("/Remember")
        .then((res) => {
          console.log(res.data);
          this.playlist = res.data;
        })
        .catch((e) => {
          console.log(e.response);
        });
    },

    agregarPlaylist() {
      this.axios
        .post("/playlist", this.playlist)
        .then((res) => {
          this.playlist.push(res.data);
          this.playlist.title = "";
          this.playlist.artist = "";
          this.playlist.ruta = "";
          this.mensaje.color = "success";
          this.mensaje.texto = "Canción Agregada";
          this.showAlert();
        })
        .catch((e) => {
          console.log(e.response);
        });
    },

    eliminarPlaylist(_id) {
      this.axios
        .delete(`/playlist/${item._id}`, item)
        .then((res) => {
          const index = this.playlist.findIndex(
            (item) => item._id === res.data._id
          );
          this.playlist.splice(index, 1);
          this.mensaje.color = "success";
          this.mensaje.texto = "Cancion Eliminada";
          this.showAlert();
        })
        .catch((e) => {
          console.log(e.response);
        });
    },

    activarEdicion(id) {
      this.editar = true;
      this.axios
        .get(`/playlist/${id}`)
        .then((res) => {
          this.playlistEditar = res.data;
        })
        .catch((e) => {
          console.log(e.response);
        });
    },

    editarTitle(item) {
      this.axios
        .put(`/playlist/${item._id}`, item)
        .then((res) => {
          const index = this.playlist.findIndex((n) => n._id === res.data._id);
          this.playlist[index].title = res.data.title;
          this.playlist[index].artist = res.data.artist;
          this.playlist[index].ruta = res.data.ruta;
          this.mensaje.color = "success";
          this.mensaje.texto = "Playlist Editada";
          this.showAlert();
          this.editar = false;
        })
        .catch((e) => {
          console.log(e.response);
        });
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    },
  },


  
};
</script>
